import axios from 'axios'

const URL = "https://api.chamadagemidao.com"
const URL_DEV = 'http://192.168.0.20:8081'

let headers = {}
let token = localStorage.getItem('token');
if (token) {
  headers['Authorization'] = 'Bearer ' + token;
}
const http = axios.create({
  baseURL: URL,
  timeout: 50000,
  headers: headers
});

export default http



